<template>
  <h1>Reaction Timer</h1>
  <button @click="start" :disabled="isPlaying">play</button>
  <block-c v-if="isPlaying" :delay="delay" @end="endGame" />
  <!-- <p v-if="showResults">Reaction time: {{ score }} ms</p> -->
  <result-c v-if="showResults" :score="score" />
</template>

<script>
import BlockC from './components/BlockC.vue';
import ResultC from './components/ResultC.vue';

export default {

  name: 'App',
  components: {
    BlockC,
    ResultC
  },
  data() {
    return {
      isPlaying: false,
      // miliseconds for player to react
      delay: null,
      score: null,
      showResults: false
    }
  },
  methods: {
    start() {
      // so color change happens in 2 to 7 seconds
      this.delay = 2000 + Math.random() * 5000;
      this.isPlaying = true;
      this.showResults = false;
    },
    endGame(reactionTime) {
      this.score = reactionTime;
      this.isPlaying = false;
      this.showResults = true;
    }
  }
}
</script>

